.peopleSay {
  margin-bottom: 168px;

  &__action {
    @include main-size-poppins-700;
    letter-spacing: $letter-spacing;
    text-transform: uppercase;
    color: #c0cdd7;

    margin-bottom: 16px;
  }

  &__title {
    @include bid-size-text;
    color: $title-color;
    
    margin-bottom: 85px;

    @media(max-width: 621px) {
      font-size: 26px;
      margin-bottom: 40px;
    }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit,  314px);
    grid-gap: 45px 10px;
    justify-content: space-between;

    @media(max-width: 1023px) {
      justify-content: space-around;
    }

    @media(max-width: 415px) {
      grid-template-columns: 280px;
    }
  }

  &__item {
    text-align: center;
    background-color: #fff;
    box-shadow: 0 9px 18px rgba(37, 41, 49, 0.03);

    border-radius: 24px;
    padding: 87px 32px 32px;

    @include hover(transform, scale(1.1));

    &_img {
      position: relative;
      z-index: 1;
      margin-bottom: 82px;
    }

    &_photo1 {
      @include peopleSay-forms(linear-gradient(180deg,
      rgba(255, 255, 255, 0.25) 0%, 
      rgba(255, 255, 255, 0) 100%)
      #56ccf2);
    }

    &_photo2 {
      @include peopleSay-forms(linear-gradient(180deg,
      rgba(255, 255, 255, 0.25) 0%, 
      rgba(255, 255, 255, 0) 100%)
      #fcc304);
    }

    &_photo3 {
      @include peopleSay-forms(linear-gradient(180deg,
      rgba(255, 255, 255, 0.25) 0%, 
      rgba(255, 255, 255, 0) 100%)
      #f25656);
    }

    &_icon {
      margin-bottom: 10px;
    }

    &_text {
      @include main-size-openSans-600;
      color: $text-color;

      margin-bottom: 16px;
    }

    &_name {
      @include main-size-openSans-600;
      color: $title-color;

      margin-bottom: 4px;
    }

    &_job {
      @include main-size-poppins-700;
      letter-spacing: $letter-spacing;
      text-transform: uppercase;
      color: #c0cdd7;
    }
  }

  &__img {
    max-width: 100%;
    height: auto;
    border-radius: 50%;
  }
}
