.header {
  position: relative;

  background-color: $BG-main-section;
  border-bottom-left-radius: 200px;

  margin-bottom: 150px;

  &__wrapper {
    height: 100vh;
  }

  &__logo {
    
    &_img {
      @include hover(transform, scale(1.2));
    }
  }

  &__slider {
    position: absolute;
    bottom: 0;
    right: 0;

    max-width: 85vh;
    width: 100%;

    @media(max-width: 1425px) {
      max-width: 70vh;
    }

    @media(max-width: 975px) {
      max-width: 50vh;
    }
    
    @media(max-width: 775px) {
      display: none;
    }
  }

  &__top {
    display: grid;
    grid-template-columns: 130px 1fr 80px;
    justify-content: space-between;
    align-items: center;

    padding-top: 50px;

    &_btn {
      position: relative;

      @include main-size-poppins-700;
      text-transform: uppercase;
      letter-spacing: $letter-spacing;
      
      background-color: transparent;
      color: #fff;
      border: none;
      cursor: pointer;

      @include hover(color, $bg-btn);
      @include active(#fff);

      &::before {
        content: "";
        position: absolute;
        bottom: -8px;
        left: 0;

        height: 2px;
        width: 100%;

        background-color: $bg-btn;
        border-radius: 4px;
      }
      @media(max-width: 775px) {
        display: none;
      }
    }
  }

  &__burger {
    display: none;
    cursor: pointer;

    &_img {
      width: 45px;
      height: 30px;
      border-radius: 6px;
    }

    @media(max-width: 775px) {
      display: block;
    }
  }

  &__promo {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  @media(max-width: 975px) {
    &__slider {
      max-width: 55vh;
    }
    &__promo {
      text-align: center;
      top: 20%;
      left: 30%;
      transform: translateX(-50%);
    }
  }

  @media(max-width: 775px) {
    margin-bottom: 80px;

    &__promo {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    &__top {
      display: flex;
    }
  }

  @media(min-height: 1100px) {
    &__slider {
      max-width: 50vh;
    }
    &__promo {
      top: 30%;
    }
  }
}

.promo {
  color: #fff;
  &__title {
    font-size: 64px;
    line-height: 68px;
    @include poppins-600;
    letter-spacing: -2px;

    max-width: 490px;
    width: 100%;

    margin-bottom: 32px;
  }

  &__text {
    font-size: 18px;
    line-height: $line-height;

    margin-bottom: 83px;
  }

  &__btn {
    @include middle-size-text;
    text-align: center;

    background-color: $bg-btn;
    color: #fff;
    border-radius: 64px;
    border: none;

    padding: 13px 70px;
    cursor: pointer;

    transition: background-color 0.5s, transform 0.5s;

    &:hover {
      transform: scale(1.1);
      background-color: #ff6619;
    }
  }

  @media(max-width: 975px) {
    &__title {
      font-size: 48px;
      line-height: 50px;
      letter-spacing: 0;
    }

    &__text {
      margin-bottom: 40px;
    }
  }

  @media(max-width: 475px) {
    &__title {
      font-size: 40px;
      line-height: 50px;
      letter-spacing: 0;
    }

    &__text {
      margin-bottom: 30px;
    }

    &__btn {
      padding: 13px 0;
      width: 200px;
    }
  }
}

.nav {
  &__list {
    display: flex;

    @media(max-width: 775px) {
      display: none;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 64px;
      @media(max-width: 975px) {
        margin-right: 20px;
      }
    }
  }

  &__link {
    @include main-size-poppins-700;
    letter-spacing: $letter-spacing;
    text-transform: uppercase;

    color: #fff;
    @include hover(color, $bg-btn);
    @include active(#fff);
  }
}

.slider {
  position: relative;

  &__img {
    max-width: 85vh;
    width: 100%;
    height: 100%;

    border-top-left-radius: 30%;
  }

  &__content {
    position: absolute;
    bottom: 50px;
    left: 144px;

    color: #fff;

    &_title {
      @include main-size-poppins-700;
      letter-spacing: $letter-spacing;
      text-transform: uppercase;
      margin-bottom: 8px;
    }
    &_text {
      line-height: $line-height;
    }
  }
  .slick-arrow {
    font-size: 0;

    background-color: transparent;
    border: none;
    cursor: pointer;
  }

  .slick-prev {
    left: 90px;
    @include arrows-slider(url(../images/slider/right.svg));
  }

  .slick-next {
    left: 48px;
    @include arrows-slider(url(../images/slider/left.svg));
  }
}

.mobile {
  display: none;

  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;

  background-color: $bg-btn;
  border-bottom-left-radius: 200px;
  text-align: center;

  &__list {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  &__link {
    @include bid-size-text;
    color: #fff;
    text-transform: uppercase;

    @include hover(color, $BG-main-section);
  }

  &__btn {
    position: absolute;
    bottom: 5%;
    left: 50%;
    transform: translate(-50%, -50%);

    @include bid-size-text;
    text-transform: uppercase;
    color: #fff;

    background-color: transparent;
    border: none;
    cursor: pointer;
    @include hover(color, $BG-main-section);
  }

  &__close {
    position: absolute;
    top: 45px;
    right: 25px;

    cursor: pointer;

    &_icon {
      width: 45px;
      height: 45px;
    }
  }

  @media(max-width: 475px) {
    &__link {
      @include middlePlus-size-text;
    }

    &__btn {
      @include middlePlus-size-text;
    }
  }
}
